<template>
	<div class="saleedi-find">
		<div class="ent-list">
			<div class="ent-block" v-for="(item, index) in getCompanyList" :key="index">
				<div class="ent-name">{{item.name}}</div>
				<div class="ent-btn" @click="createOrder(item)">前往订货<van-icon name="arrow" class="btn-arrow" /></div>
			</div>
		</div>
		<div class="order-manage fx-edi-block">
			<div class="block-title">订单管理</div>
			<div class="order-manage-module">
				<div class="manage-module-row">
					<div
						v-for="(item, index) in manageModule"
						:key="index"
						class="manage-module-item"
						@click="billClickHandler(item.billState)" >
						<div class="manage-module-item-block" :class="{'fx-red-dot': item.waring}" v-show="item.showFlag">
							<img
								class="manage-module-item-icon"
								:src="item.iconSrc">
						</div>
						<p v-show="item.showFlag">{{item.label}}</p>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>

export default {
	name: 'find',
	data () {
		return {
			manageModule: [{
				label: '待确认',
				iconSrc: require('@/assets/images/icon/icon-beforeConfirm.png'),
				waring: false,
				billState: '1',
				showFlag: true
			}, {
				label: '待收货',
				iconSrc: require('@/assets/images/icon/icon-beforeReceiving.png'),
				waring: false,
				billState: '2',
				showFlag: true
			}, {
				label: '已完成',
				iconSrc: require('@/assets/images/icon/icon-confirm.png'),
				waring: false,
				billState: '3',
				showFlag: true
			}, {
				label: '占位',
				iconSrc: require('@/assets/images/icon/icon-confirm.png'),
				waring: false,
				billState: '',
				showFlag: false
			}],
			getCompanyList: []
		}
	},
	created () {
		this.getCompany()
	},
	methods: {
		getCompany () {
			this.$fxHttp.requestPost({
				url: '/getOrderCompany'
			}).then(res => {
				this.getCompanyList = res
			})
		},
		createOrder (item) {
			this.$router.push({
				path: '/createOrder',
				query: {
					companyName: item.name,
					companyId: item.id
				}
			})
		},
		billClickHandler (billState) {
			this.$router.push({
				path: '/orderList',
				query: {
					billState
				}
			})
		}
	}
}
</script>
